.Movie{
    background-color: white;
    width:40%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-wrap:wrap;
        flex-wrap:wrap;
    margin-bottom: 50px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    padding: 0 20;
    -webkit-box-shadow: 0 8px 38px rgba(133,133,133,0.3),0 5px 12px rgba(133,133,133,0.22);
            box-shadow: 0 8px 38px rgba(133,133,133,0.3),0 5px 12px rgba(133,133,133,0.22);
}

.Movie_Column{
    width: 30%;
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.Movie_Column:last-child{
    padding:20px 0;
    width:60%;
}

.Movie h1{
    font-size:20%;
    font-weight: 600;
}

.Movie .Movie_Genres{
    display:-ms-flexbox;
    display:flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-bottom:20px;
}

.Movie .Movie_summary{
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    color: #b4b5bd;
    overflow: hidden;

}

.Movie .Movie_Poster{
    max-width: 100%;
    position: relative;
    top:-20px;
    -webkit-box-shadow: -10px 19px 38px rgba(83,83,83,0.3) , 10px 15px 12 px rgba(80,80,80,0.22);
            box-shadow: -10px 19px 38px rgba(83,83,83,0.3) , 10px 15px 12 px rgba(80,80,80,0.22);
}


